<template>
  <div class="card mb-3">
    <div class="card-header">
      By: {{ author }}
    </div>
    <div class="card-body">

      <div class="accordion accordion-flush .fs-1" :id="'accordionFlush_'+blog_id">
        <div class="accordion-item">
          <h2 class="accordion-header" :id="'flush-heading_'+blog_id">
            <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" :data-bs-target="'#flush-collapse_'+blog_id" aria-expanded="false" aria-controls="flush-collapseOne">
              <p class="fs-2">{{ title }}</p>
            </button>
          </h2>
          <div :id="'flush-collapse_'+blog_id" class="accordion-collapse collapse" :aria-labelledby="'flush-heading_'+blog_id" :data-bs-parent="'#accordionFlush_'+blog_id">
            <div class="accordion-body fs-5">
              {{content}}
            </div>
          </div>
        </div>
      </div>

    </div>

    <div class="card-footer text-muted">
      {{ formatted_datetime }}

      <div class="float-end">
        <span v-if="is_logged_in">
          <span v-if="!is_mine">
            <span v-if="liking">
              <button class="btn btn-success" type="button" disabled>
                <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                <span class="visually-hidden">Loading...</span>
              </button>
            </span>
            <span v-if="!liking">
              <button class="btn btn-outline-success" v-if="!is_blog_liked" @click="like_blog">
                <span class="material-icons">thumb_up_off_alt</span>
              </button>
              <button class="btn btn-success" v-if="is_blog_liked" @click="dislike_blog">
                <span class="material-icons">thumb_up_alt</span>
              </button>
            </span>
          </span>


          <span v-if="is_mine">

            <button class="m-1 btn btn-outline-warning btn-sm" @click="edit_blog">
              <span class="material-icons">edit</span>
            </button>

            <button class="m-1 btn btn-outline-danger btn-sm" @click="delete_blog">
              <span class="material-icons">delete</span>
            </button>
          </span>

        </span>


        <span class="m-1">
          <span v-if="likes_loading">
            <div class="spinner-grow spinner-grow-sm" role="status">
              <span class="visually-hidden">Loading...</span>
            </div>
          </span>

          <span v-if="!likes_loading">
            {{blog_likes}} likes
          </span>

          
        </span>
      </div>

    </div>

  </div>
</template>

<script>
import {doc, deleteDoc, collection, query, where, getDocs, setDoc, onSnapshot} from "firebase/firestore";
import {firestore_db} from "@/main";
import {getAuth} from "firebase/auth";
import router from "@/router";

export default {
  name: "BlogObj",
  data(){
    return{
      formatted_datetime: '',
      is_blog_liked: false,
      is_logged_in: false,
      blog_likes: 0,
      liking: false,
      disliking: false,
      likes_loading: true
    }
  },
  props:{
    blog_id:String,
    title:String,
    content:String,
    author:String,
    timestamp: Number,
    uid: String,
    is_mine: Boolean,
  },
  mounted() {

    let d = new Date( this.timestamp*1000);

    
    this.formatted_datetime = d.toGMTString();

    if(getAuth().currentUser)
      this.is_logged_in = true
        //d.getDate() + '/' + (d.getMonth()+1) + '/' + d.getFullYear() + ' ' + d.getHours() +':'+d.getMinutes()+':'+d.getSeconds();



    if(this.is_logged_in)
      this.doc_like_name = this.blog_id + "_" + getAuth().currentUser.uid;

      const q = query(collection(firestore_db, "likes"));

      onSnapshot(q, (querySnapshot) => {
        const likes = [];

        querySnapshot.forEach((doc) => {
          if(this.blog_id === String(doc.id).split("_")[0])
            likes.push(doc.data());

        });

        //console.log("Likes for", this.blog_id, likes.length);
        this.blog_likes = likes.length;
        this.likes_loading = false;

        if(this.is_logged_in){
          const auth_uid = getAuth().currentUser.uid;

          likes.forEach(like => {
            if(like.uid === auth_uid ){
              this.is_blog_liked = true;
            }
          });

        }
        
        
      });

    
    
    

  },
  methods:{
    async is_blog_liked_by_me(blog_id){
      let ids = []

      const q = query(
        collection(firestore_db, "blog_likes"),
        where("liked_by", "==", getAuth().currentUser.uid),
        where("liked_blog_id", "==", blog_id)
      );

      const querySnapshot = await getDocs(q);
      
      querySnapshot.forEach((doc) => {
        ids.push(doc.id)
      });

      return ids.length > 0;
    },

    async like_blog(){
      this.liking = true;
      
      await setDoc(doc(firestore_db, "likes", this.doc_like_name), {
        liked_blog: this.blog_id,
        uid: getAuth().currentUser.uid
      }).then(()=>{
        console.log("blog liked")
        this.is_blog_liked = true;
        this.liking = false;
      }).catch((err)=>{
        alert(err.message)
        this.liking = false;
      });

    },

    async dislike_blog(){
      
      this.liking = true;

      await deleteDoc(doc(firestore_db, "likes", this.doc_like_name)).then(()=>{
        console.log("blog disliked")
        this.is_blog_liked = false;
        this.liking = false;
      }).catch((err)=>{
        alert(err.message)
        this.liking = false;
      });
    },

    
    async delete_blog(){
      let sure = confirm("You sure dude?")

      if(sure){
        console.log(sure)
        await deleteDoc(doc(firestore_db, "blogs", this.blog_id)).then((result)=>{
          console.log(result)
          alert("Deleted!")
        }).catch((err)=>{
          alert(err.message)
        });
      }
    },

    edit_blog(){
      //router.push('/edit')
      router.push({ name: 'edit', params: {
        'blog_id': this.blog_id, 'blog_title': this.title, 'blog_content': this.content,
      }})
    },

    
  }
}
</script>

<style scoped>

</style>