<template>
  <h1 class="text-center">Latest Blogs</h1>
  <div class="m-4">
    <div class="form-check form-switch">
      <input class="form-check-input" type="checkbox" id="flexSwitchCheckDefault" @change="filter_trigger" v-model="filter_mine">
      <label class="form-check-label" for="flexSwitchCheckDefault">Show My Blogs only</label>
    </div>
  </div>


  <div style="text-align: center;" v-if="loading">
    <div class="spinner-grow text-success" role="status">
      <span class="visually-hidden">Loading...</span>
    </div>
  </div>

  <div class="m-4">
   <span v-for="blog in all_blogs" :key="blog.blog_id">
     <BlogObj
         :blog_id="blog.blog_id"
         :author="blog.author"
         :title="blog.title"
         :content="blog.content"
         :timestamp= "blog.timestamp"
         :uid= "blog.uid"
         :is_mine="blog.is_mine"
         :liked = "blog.liked"
         :likes = blog.likes
     />

     <br>
   </span>

  </div>



</template>

<style>
  body {
    margin: 5px;
  }
</style>

<script>

import BlogObj from "@/components/BlogObj";
import {firestore_db} from "@/main";
import { collection, onSnapshot, orderBy, query } from "firebase/firestore";
import {getAuth} from "firebase/auth";
//import {getAuth} from "firebase/auth";

export default {
  name: 'HomeView',
  components: {
    BlogObj
  },
  data(){
    return {
      all_blogs: [],
      loading: true,
      filter_mine: false
    }
  },

  async mounted() {
    await this.fetch_blogs()
  },

  methods:{
    async fetch_blogs(){
      const q = query(collection(firestore_db, "blogs"), orderBy("timestamp", "desc"));
      onSnapshot(q, (querySnapshot) => {
        this.all_blogs = []

        querySnapshot.forEach((blog) => {
          let mine = null;

          if(getAuth().currentUser)
            mine = blog.data().uid === getAuth().currentUser.uid

          if(this.filter_mine && mine){
            this.all_blogs.push(
                {
                  blog_id: blog.id,
                  author: blog.data().author,
                  title: blog.data().title,
                  content: blog.data().content,
                  timestamp: blog.data().timestamp,
                  uid: blog.data().uid,
                  is_mine: mine
                }
            )
          }else if(!this.filter_mine){
            this.all_blogs.push(
                {
                  blog_id: blog.id,
                  author: blog.data().author,
                  title: blog.data().title,
                  content: blog.data().content,
                  timestamp: blog.data().timestamp,
                  uid: blog.data().uid,
                  is_mine: mine
                }
            )
          }

        });
        this.loading = false
        console.log(this.all_blogs)

      });
    },

    async filter_trigger(){
      await this.fetch_blogs()
    }
    

  }
}
</script>
