<template>

<nav class="navbar navbar-expand-lg navbar-light bg-light">
  <div class="container-fluid">
    <a class="navbar-brand" href="#">Blog App</a>

    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav me-auto mb-2 mb-lg-0">

        <li class="nav-item">
          <router-link class="nav-link active" to="/">Home</router-link>
        </li>

        <li class="nav-item">
          <router-link v-if="isLoggedIn" class="nav-link" to="/write">Write Blog</router-link>
        </li>


      </ul>

      <form class="d-flex" v-if="isLoggedIn" >
        <h5 class="m-1">{{email_name}}</h5>
        <button class="btn btn-outline-danger" @click="logout">Logout</button>
      </form>

      <form class="d-flex" v-if="!isLoggedIn" >
        <router-link v-if="!isLoggedIn" class="btn btn-success" to="/login">Login</router-link>
      </form>

    </div>
  </div>
</nav>



  <router-view/>
</template>

<style>
  body{
    margin: 5px;
  }
</style>

<script>

import {getAuth, onAuthStateChanged, signOut} from "firebase/auth";

export default {
  data(){
    return{
      auth: null,
      isLoggedIn: false,
      email_name: ''
    }
  },
  methods:{
    logout(){
      signOut(this.auth)
          .then(()=>{
            console.log("signed out!")
          })
          .catch((err)=>{
            console.log(err.message)
          })
    }
  },
  mounted() {
    this.auth = getAuth();

    onAuthStateChanged(this.auth, (user)=>{
      this.isLoggedIn = (user!=null);

      if(user){
        //this.profile_pic_url =
        this.email_name = (getAuth().currentUser.displayName!=null)?getAuth().currentUser.displayName:getAuth().currentUser.email;

      }
    });
  }


}
</script>